<template>
  <div class="my-container">
    <!-- my页面 -->

    <!-- 登录时的页头 -->
    <div class="header user-info" v-if="user">
      <!-- 上部分信息 -->
      <div class="base-info">
        <div class="left">
          <van-image class="avator" round fit="cover" :src=userInfo.photo />
          <div class="name">{{userInfo.name}}</div>
        </div>

        <div class="right">
          <van-button size="mini" round to="/user/profile">编辑资料</van-button>
        </div>
      </div>

      <!-- 下部分数据 -->
      <div class="user-status">
        <div class="user-status-item">
          <div class="count">{{userInfo.art_count}}</div>
          <div class="text">头条</div>
        </div>

        <div class="user-status-item">
          <div class="count">{{userInfo.fans_count}}</div>
          <div class="text">关注</div>
        </div>

        <div class="user-status-item">
          <div class="count">{{userInfo.follow_count}}</div>
          <div class="text">粉丝</div>
        </div>

        <div class="user-status-item">
          <div class="count">{{userInfo.like_count}}</div>
          <div class="text">获赞</div>
        </div>
      </div>
    </div>

    <!-- 未登录页头区域 -->
    <div class="header not-login" v-else>
      <div class="login-btn" @click="$router.push('/login')">
        <img src="~@/assets/mobile.png" alt="" class="login-img">
        <span class="text">登录/注册</span>
      </div>

    </div>

    <!-- 宫格布局，收藏和历史的导航 -->
    <van-grid clickable :column-num="2" class="grid-nav" >
      <van-grid-item to="/" class="grid-item">
        <i slot="icon" class="toutiao toutiao-shoucang"></i>
        <span class="text" slot="text">收藏</span>
      </van-grid-item>
      <van-grid-item to="/" class="grid-item">
      <i slot="icon" class="toutiao toutiao-lishi"></i>
      <span class="text" slot="text">历史</span>
      </van-grid-item>
    </van-grid>

    <!-- 小智同学，消息通知，退出登录 -->
    <van-cell title="消息通知"  is-link/>
    <van-cell title="小智同学"  is-link style="margin-bottom:9px"/>

    <van-cell title="退出登录"  class="loginOut-btn" v-if="user" @click="loginOut" clickable/>

    <!-- </vant-ceil> -->
  </div>
</template>

<script>
// 引入vuex
import { mapState } from 'vuex'

// 引入获取用户信息的api
import { getUserInfo } from '@/api/user.js'
export default {
  name: 'myIndex',

  data () {
    return {
      userInfo: {}
    }
  },

  created () {
    if (this.user) {
      // 用户已经登录，获取用户信息
      this.loadUserInfo()
    }
  },

  methods: {

    // 获取用户信息

    async loadUserInfo () {
      try {
        const { data: res } = await getUserInfo()
        console.log(res)
        this.userInfo = res.data
      } catch (err) {
        this.$toast('获取用户信息失败')
      }
    },
    // 退出登录
    loginOut () {
      this.$dialog.confirm({
        title: '确认退出吗?',

        confirmButtonColor: 'blue'
      })
        .then(() => {
          // on confirm
          // 确认退出
          this.$store.commit('setUser', null)
        })
        .catch(() => {
          // on cancel
        })
    }
  },

  computed: {
    ...mapState(['user'])
  }
}
</script>

<style lang="less" scoped>
.my-container {
  .header {
    height: 361px;
    background-image: url('~@/assets/banner.png');
    background-size: cover;
  }

  // 退出登录的按钮
  .loginOut-btn{
    text-align: center;
    color: #e28c8c;
  }

  // 宫格导航
  .grid-nav{

    .grid-item{
      height: 141px;

      i.toutiao{
        font-size: 45px;
      }

      .toutiao-shoucang{
        color: #eb5253;
      }

      .toutiao-lishi{
        color: #ff9d1d;
      }

      span.text{
          font-size: 28px;
        }
    }
  }

  // 用户已经登录
  .user-info {
    .base-info {
      height: 231px;
      padding: 76px 32px 23px;
      box-sizing: border-box;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .left {
        display: flex;
        align-items: center;

        .name {
          margin-left: 23px;
          color: #fff;
          font-size: 30px;
        }

        .avator {
          width: 132px;
          height: 132px;
          background-size: cover;
          border: 1px solid #fff;
        }
      }
    }

    .user-status {
      display: flex;

      .user-status-item {
        height: 130px;
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: #fff;

        .count {
          font-size: 36px;
        }

        .text {
          font-size: 23px;
        }
      }
    }
  }

  // 用户未登录
  .not-login {
    display: flex;
    justify-content: center;
    align-items: center;

    .login-btn {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      .login-img {
        width: 132px;
        height: 132px;
      }

      .text {
        margin-top: 20px;
        font-size: 20px;
        color: #fff;
      }
    }
  }
}
</style>
